@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}html{height:100%}body::-webkit-scrollbar{width:10px}@media(max-width: 1475px){body::-webkit-scrollbar{width:9px}}body::-webkit-scrollbar-track{background:#e2e2e2;border-radius:1px}body::-webkit-scrollbar-thumb{background-color:#474c55;border-radius:1px}body{width:100%;height:100%;font-family:"Montserrat",sans-serif}h1,h2,h3,h4,h5,h6{font-weight:600}.rec-swipable{align-items:stretch}.rec-carousel-item-visible{margin:0;display:flex}.hover-button{display:flex;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:max(12.5px,.85vw) max(25px,1.65vw);background-color:#4376ee;color:#fff !important;text-decoration:none !important;font-size:max(15px,.95vw);border-radius:max(25px,1.5vw);border:0;cursor:pointer;transition:.75s ease-in-out;text-align:center}.hover-button:hover{box-shadow:400px 0 0 0 rgba(0,0,0,.25) inset,-400px 0 0 0 rgba(0,0,0,.25) inset}
